@import '../variables.scss';

.settingsWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;

  .userSettings {
    display: flex;
    @media (max-width: $mobileWidth) {
      flex-direction: column;
    }
  }

  .avatar {
    width: 200px;
    height: 200px;
    background-color: #999;
    border-radius: 500px;
    background-position: 0px 80%;
    background-size: 100%;
    display: block;
    position: relative;
    cursor: pointer;
    margin-right: 30px;
    &::before {
      content: '';
      transition: background-color 0.2s;
    }
    &:hover::before {
      content: 'Нажмите чтоб сменить';
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.6);
      display: flex;
      border-radius: 500px;
      justify-content: center;
      align-items: center;
      color: white;
    }
  }

  input[type='file'] {
    display: none;
  }

  .contentBlock {
    width: calc(100% - 40px);
    padding: 20px;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(80px);
    margin-bottom: 20px;

    h1 {
      margin: 0;
      color: #333;
      font-size: 28px;
    }

    p {
      margin-bottom: 8px;
    }

    input {
      padding-left: 15px;
      height: 50px;
      border-radius: 12px;
      border: 1px solid #d8d8d8;
      font-size: 15px;
      @media (max-width: $mobileWidth) {
        width: calc(100% - 15px);
      }
    }

    .row {
      display: flex;
      flex-wrap: wrap;

      div {
        margin-right: 15px;
        @media (max-width: $mobileWidth) {
          width: 100%;
          margin-right: 0;
        }
      }
    }

    .bgPreview {
      height: 128px;
      width: 256px;
      border: 1px solid #d8d8d8;
      border-radius: 12px;
      background-size: cover !important;
      background-position: center !important;
    }

    p.titleBG {
      text-align: center;
    }

    .backgrounds {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      > div {
        cursor: pointer;
        padding-bottom: 10px;

        &.selected {
          background: rgba(0, 0, 0, 0.07);
          border-radius: 12px;
        }
      }
    }
  }

  button {
    background-color: $accentColor;
    padding: 10px 20px;
    border-radius: 12px;
    border: none;
    color: white;
    margin-top: 15px;
    cursor: pointer;

    &.bgRed {
      width: 100%;
      display: block;
      background-color: rgb(255, 86, 86);
    }
  }
}
